.frame-remind-home{ /*div home*/   
    font-family: 'Concert One', cursive;
position: absolute;
width: 564px;
height: 355px;
left: calc(50% - 564px/2);
top: calc(50% - 355px/2);
background: rgba(255, 255, 255, 0.5);
box-shadow: 12px 12px 4px rgba(0, 0, 0, 0.09);
border-radius: 77px;
opacity: 0.7;
    }; 

