.notes-data-user {
color: rgb(112, 11, 226);
text-align: right;
line-height: 10%;
font-size: 100%;

}


.note-maker-space{
    font-family: 'Concert One', cursive;
border-radius: 10%;
border-color: rgb(12, 243, 193);
display: grid;
grid-template-areas: repeat(1, 1fr);
grid-gap: 10px;
border: 30px;
padding: 30px;
}
.titleNotes{
    border-color: whitesmoke;
    width:  450px;
    height: 30px;
    width: 50vw;
    margin-left : 20vw;
    opacity: 0.6;
}
.descripcionNotes{
   
    border-color: whitesmoke;
    resize:none; /*para que no se pueda redireccionar el textarea*/
    width:  450px;
    height: 100px;
    width: 50vw;
    margin-left : 20vw;
    opacity: 0.6;
}

.btn-notas-primary{ /*botón publicar y editar*/
    color: rgb(107, 15, 228);
    border-color: whitesmoke;
    width:  70px;
    height: 30px;
    margin-left : 650px;
    margin-right : 25px; 
}   

.containerAllNotes{
    font-family: 'Carter One', cursive;
    border-radius: 20%;
    display: grid;
    grid-gap: 50px;
    justify-items: stretch;
    padding: 30px;
    border: 20%;
    
    } 

.individualNotesContainer{
    background: rgb(242,242,242);
background: radial-gradient(circle, rgba(242,242,242,1) 4%, rgba(207,240,209,1) 97%);
    font-family: 'Carter One';
    background-color:  white;
    opacity: 0.8;
    margin-bottom: 0%;
}

.individualNotesEdit{
    margin-bottom: 0%;
    border-radius: 100px;
    border-color: whitesmoke;
    width: 18%;
    margin: 0 10px;
    background-color: whitesmoke;
    display: inline;
    margin-left: 48px;
}
.individualNotesDelet{
    color: red;
    border-radius: 100px;
    border-color: white;
    width: 18%;
    background-color: whitesmoke;
    display: inline;
    margin-left: 50px;
}

ion-icon{
    font-size: 25px;
}
@media screen  and (max-width: 820px) { /*tablet*/
